import React from 'react';
import './Features.scss';
import tournire from '../../images/tournire.svg';
import friends from '../../images/friends.svg';
import analize from '../../images/analize.svg';

export default function Features() {
  return (
    <section className='features'>
        <div className='features__container'>
            <div className='features__box'>
                <img className='features__image' src={friends} alt='' />
                <p className='features__text'>  !</p>
            </div>
            <div className='features__box'>
                <img className='features__image' src={analize} alt='' />
                <p className='features__text'> </p>
            </div>
            <div className='features__box'>
                <img className='features__image' src={tournire} alt='' />
                <p className='features__text'> <br/> </p>
            </div>
        </div>
    </section>
  )
}
